
    import { defineComponent } from "vue";

    export default defineComponent({
        name: 'BarraLateral',

        emits: ['temaAlterado'],

        data() {
            return {
                modoEscuro: false
            }
        },

        computed:{
            textoBotao(){
                if(this.modoEscuro){
                    return 'Desativar modo escuro'
                }
                else {
                    return 'Ativar modo escuro'
                }
            }
        },

        methods:{
            alterarTema(){
                this.modoEscuro = !this.modoEscuro;
                this.$emit('temaAlterado', this.modoEscuro);
            }
        }

    })
